.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.show-list-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  background-color: #110901;
}

.show-card {
  padding-top: 50px;
  background-color: #7b0707;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  width: 300px;
  margin-left: 30px;
}

.show-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.show-details {
  padding: 10px;
}
.heading {
  text-align: center;
}



.show-title {
  font-size: 24px;
  font-weight: bold;
  margin: 0 0 10px;
  color: white;
}

.view-details {
  display: block;
  margin-top: 10px;
  background-color: #007bff;
  color: #fff;
  text-align: center;
  padding: 10px;
  border-radius: 5px;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

.view-details:hover {
  background-color: #0069d9;
}
.show-details-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  background-color: greenyellow
}

.show-image {
  width: 300px;
  height: 450px;
  object-fit: cover;
  margin-bottom: 20px;
}

.show-title {
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  font-family: "Times New Roman", Times, serif;
  margin: 0 0 20px;
}

.show-summary {
  font-size: 20px;
  text-align: center;
  font-family: "Times New Roman", Times, serif;
  line-height: 1.5;
  max-width: 600px;
}
.heading h1 {
  font-size: 2.5em;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: #333;
}

.heading span {
  display: block;
  text-align: center;
  font-size: 0.8em;
  color: #888;
  margin-top: 10px;
}





